@import './defalut';

html, body { overflow-x:hidden; }

section.input_step {
    position:relative; padding:50px 0;
    h1 { font-size:30px; font-weight:$bold; text-align:center;  }
    .container {
        position:relative; margin:0 auto; box-sizing:border-box; text-align:center;
        .main {
            width:488px; display:inline-block; padding-top:30px; text-align:left;
            h5 { width:100%; padding-left:57px; box-sizing:border-box; color:white; font-size:16px; font-weight:$regular; line-height:54px; font-family:$Mabinogi; background:url(../../public/images/common/bg_top.png) 50% 50% /cover no-repeat; @include textShadow(#838383) }
            .input_wrap {
                max-width:484px; margin:0 auto; background:url(../../public/images/common/bg_content.jpg) 50% 50% /100% 100% no-repeat;
                > ul {
                    margin:0 8px; padding-top:6px; font-size:0; border-bottom:2px solid black;
                    li {
                        display:inline-block; position:relative; top:2px; padding:0 9px; margin-left:4px; color:white; font-size:16px; font-weight:$regular; line-height:38px; font-family:$Mabinogi; background:url(../../public/images/step1/tab_disabled_bg.jpg) 50% 50% /cover no-repeat; @include textShadow(#838383);
                        &:before { content:""; width:9px; height:38px; position:absolute; top:0; left:0; background:url(../../public/images/step1/tab_disabled_round.jpg) 50% 50% /cover no-repeat; }
                        &:after { content:""; width:9px; height:38px; position:absolute; top:0; right:0; transform:rotateY(180deg); background:url(../../public/images/step1/tab_disabled_round.jpg) 50% 50% /cover no-repeat; }
                        &.active {
                            background-image:url(../../public/images/step1/tab_active_bg.jpg);
                            &:before {  background-image:url(../../public/images/step1/tab_active_round.jpg); }
                            &:after {  background-image:url(../../public/images/step1/tab_active_round.jpg); }
                        }
                    }
                }
                .input_tit {
                    padding:8px 34px 0 14px; color:#e3e3e3; font-size:12px; line-height:15px; font-family:$Mabinogi;
                    .combo_txt { width:351px; display:inline-block; vertical-align:middle; text-align:center; }
                    .skill_txt { width:calc(100% - 351px); display:inline-block; vertical-align:middle; text-align:center; }
                }
                .input_inner {
                    height:468px; padding:4px 0 4px 14px; font-size:0;
                    .combocard_wrap {
                        width:351px; height:100%; display:inline-block; position:relative; background:url(../../public/images/common/combocard_bg.jpg) 50% 50% /cover no-repeat;
                        .combo_each {
                            width:118px; height:52px; position:absolute; font-size:0;
                            .disabled_wrap {
                                position:absolute; top:50%; left:50%; padding:0 16px; color:white; border:0; font-size:14px; font-weight:$regular; line-height:30px; font-family:$Mabinogi; cursor:pointer; background:url(../../public/images/common/btn_bg.png) 50% 50% /100% 100% no-repeat; @include textShadow(black); transform:translate(-50%, -50%);
                            }
                            .active_wrap {
                                height:100%;
                                .combo_input {
                                    display:inline-block; vertical-align:top; position:relative; color:#ffff5c; font-size:16px; font-family:$Mabinogi; @include textShadow(#cf290f);
                                    &:before { content:"+"; position:absolute; top:50%; left:13px;  transform:translate(0, -50%); }
                                    &:after { content:"%"; position:absolute; top:50%; right:13px;  transform:translate(0, -50%); }
                                    > input {
                                        width:20px; height:20px; color:#ffff5c; padding:16px 23px; border:0; font-size:16px; font-family:$Mabinogi; background-color:black; @include textShadow(#cf290f);
                                    }
                                }
                                > button {
                                    width:52px; height:100%; display:inline-block; position:relative; background-color:#252525; border:1px solid black; z-index:2; cursor:pointer;
                                    &.active::before {
                                        content:""; width:58px; height:58px; position:absolute; top:50%; left:50%; z-index:-1; border-radius:5px; background-color:#e1e0e0; transform:translate(-50%, -50%);
                                    }
                                    > img { width:100%; height:100%; }
                                }
                            }
                            &:nth-of-type(1) { top:134px; left:92px; }
                            &:nth-of-type(2) {
                                top:167px; right:20px;
                                &:before { content:""; width:74px; height:34px; position:absolute; top:-12px; left:-72px; z-index:1; background:url(../../public/images/common/arrow_left.png) 50% 50%/cover no-repeat; }
                            }
                            &:nth-of-type(3) {
                                top:207px; left:20px;
                                &:before { content:""; width:74px; height:34px; position:absolute; top:-12px; right:-72px; z-index:1; background:url(../../public/images/common/arrow_right.png) 50% 50%/cover no-repeat; }
                            }
                            &:nth-of-type(4) {
                                top:240px; right:20px;
                                &:before { content:""; width:74px; height:34px; position:absolute; top:-12px; left:-72px; z-index:1; background:url(../../public/images/common/arrow_left.png) 50% 50%/cover no-repeat; }
                            }
                            &:nth-of-type(5) {
                                top:280px; left:20px;
                                &:before { content:""; width:74px; height:34px; position:absolute; top:-12px; right:-72px; z-index:1; background:url(../../public/images/common/arrow_right.png) 50% 50%/cover no-repeat; }
                            }
                            &:nth-of-type(6) {
                                top:313px; right:20px;
                                &:before { content:""; width:74px; height:34px; position:absolute; top:-12px; left:-72px; z-index:1; background:url(../../public/images/common/arrow_left.png) 50% 50%/cover no-repeat; }
                            }
                        }
                        .combo_btn_wrap {
                            width:264px; position:absolute; bottom:28px; left:50%; padding:18px 0; text-align:center; transform:translateX(-50%);
                            button {
                                margin:0 2px; padding:0 25px; color:white; border:0; font-size:12px; font-weight:$regular; line-height:28px; font-family:$Mabinogi; opacity:0; visibility:hidden; cursor:pointer; background:url(../../public/images/common/btn_bg.png) 50% 50% /100% 100% no-repeat; @include textShadow(black);
                                &.active { opacity:1; visibility:inherit; }
                            }
                        }
                    }
                    .skill_wrap {
                        height:100%; display:inline-block; padding:4px 5px; box-sizing:border-box; overflow-y:scroll; overflow-x:hidden; background:url(../../public/images/common/skill_bg.jpg) 50% 50% /100% 100% no-repeat;
                        &::-webkit-scrollbar { background-color:#282828; }
                        &::-webkit-scrollbar-track { background-color:#010101; }
                        &::-webkit-scrollbar-thumb { background-color:#282828; border:2px solid #5d5d5d; border-radius:2px; }
                        &::-webkit-scrollbar-button { background-color:#282828; border:2px solid #5d5d5d; border-radius:2px; }

                        > li {
                            width:58px; height:58px; position:relative; margin:4px auto 0; background:url(../../public/images/common/skill_line.png) 50% 50% /100% 100% no-repeat;
                            &:nth-of-type(1) { margin:0 auto; }
                            button {
                                width:45px; height:45px; position:absolute; top:50%; left:50%; padding:0; transform:translate(-50%, -50%); border-radius:3px; border:2.5px solid white; box-sizing:border-box; cursor:pointer; background-image: linear-gradient(180deg, #4c6475 30%, #94aeb6);
                                img { width:100%; height:100%; }
                            }
                        }
                    }
                }
                .btn_wrap {
                    padding-bottom:20px; text-align:center;
                    a, button { display:inline-block; margin:0 2px; padding:0 30px; color:white; border:0; font-size:14px; font-weight:$regular; line-height:38px; text-decoration:none; font-family:$Mabinogi; cursor:pointer; background:url(../../public/images/common/btn_bg.png) 50% 50% /100% 100% no-repeat; @include textShadow(black); }
                }
            }
        }
        .description {
            width:488px; height:672px; display:inline-block; margin:30px 0 0 10px; border-radius:3px; text-align:left; vertical-align:top; background-color:#eee;
            h5 {
                margin:0 15px; padding:18px 0; color:black; border-bottom:1px solid #d5d5d5; font-weight:$bold;
                > span { padding:0 8px 1px; border-radius:100%; color:#eee; font-weight:$regular; background-color:#adadad; }
            }
            > .tab_cont {
                height:86%; margin:15px 15px 0; color:black; overflow-y:auto;
                &::-webkit-scrollbar { background-color:#d9d9d9; }
                &::-webkit-scrollbar-track { background-color:#d9d9d9; }
                &::-webkit-scrollbar-thumb { background-color:#939393; border:2px solid #ababab; border-radius:2px; }
                &::-webkit-scrollbar-button { background-color:#939393; border:2px solid #ababab; border-radius:2px; }
                li {
                    margin-right:10px; font-size:14px; line-height:22px;
                    .text_description {
                        margin:10px 0; padding:10px; border-radius:3px; background-color:#c3c2c2;
                        > span.indentation_txt { display:block; padding-left:15px; }
                    }
                    .progress_txt { padding-top:10px; }
                    .btn_txt { display:inline-block; margin-top:3px; padding:0 8px; color:white; font-size:12px; border-radius:3px;  background-color:#2c2c2c; }
                    .impact_txt { color:red; }
                    > button.strong_btn {
                        position:relative; margin-top:30px; color:black; cursor:pointer; overflow:hidden; font-size:16px; font-weight:$bold; transition:all .3s;
                        &::after { content:""; width:100%; height:1px; position:absolute; bottom:0; left:-100%; background-color:black; transition:all .3s; }
                        &:hover {
                            color:#0006ff;
                            &::after { left:0; background-color:#0006ff; }
                        }
                    }
                    > u { text-decoration:underline; }
                    b { font-weight:$bold; }
                    .number_box {
                        display:flex; flex-direction:row; flex-wrap:nowrap; align-items:flex-start; padding-top:5px; text-align:left;
                        &:nth-of-type(1) { padding-top:10px; }
                        strong { padding:0 8px 3px 8px; color:#801a09; border-radius:100%; font-weight:$bold; background-color:#ffeb43; }
                        p { padding-left:5px; }
                    }
                    img { width:100%; margin-top:20px; }
                }
            }
        }
    }
}